import React, { useEffect, useState } from 'react';
import './Standings.css';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, {Navigation} from "swiper";
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';

import rplLogo from '../../../assets/ico/rplLogo.webp';
import eplLogo from '../../../assets/ico/eplLogo.webp';
import laligaLogo from '../../../assets/ico/laligaLogo.webp';
import bundesligaLogo from '../../../assets/ico/bundesligaLogo.webp';
import serieaLogo from '../../../assets/ico/serieaLogo.webp';
import ligue1Logo from '../../../assets/ico/ligue1Logo.webp';
import axios from 'axios';

SwiperCore.use([Navigation]);

const StandingsSlider = () => {
    const [rplStandings, setRplStandings] = useState(); 
    const [eplStandings, setEplStandings] = useState(); 
    const [laligaStandings, setLaligaStandings] = useState(); 
    const [bundesligaStandings, setBundesligaStandings] = useState(); 
    const [serieaStandings, setSerieaStandings] = useState(); 
    const [ligue1Standings, setLigue1Standings] = useState(); 

    useEffect(() => { 
        const fetchData = async () => {
            await axios.get('/standings/rpl')
            .then(response => {
                setRplStandings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'rplStandings' + i} className="col">
                                <div className="left">
                                    <Tippy content={e.description && e.description.includes('') ? e.description + ' (?)*' : e.description}><span className={`place ${e.descrLat && e.descrLat.includes('Ligi') ? null : e.descrLat}`}>{e.place}</span></Tippy>
                                    <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                    <span className='name'>{e.name}</span>
                                </div>
                                <div className="nums">
                                    <span className="games">{e.games}</span>
                                    <div className="forAgainst">
                                        <span className='for'>{e.goalsFor}</span>
                                        <span>:</span>
                                        <span className='against'>{e.goalsAgainst}</span>
                                    </div>
                                    <div className="points">{e.points}</div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/epl')
            .then(response => {
                setEplStandings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'eplStandings' + i} className="col">
                                <div className="left">
                                    <Tippy content={e.description}><span className={`place ${e.descrLat}`}>{e.place}</span></Tippy>
                                    <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                    <span className='name'>{e.name}</span>
                                </div>
                                <div className="nums">
                                    <span className="games">{e.games}</span>
                                    <div className="forAgainst">
                                        <span className='for'>{e.goalsFor}</span>
                                        <span>:</span>
                                        <span className='against'>{e.goalsAgainst}</span>
                                    </div>
                                    <div className="points">{e.points}</div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/laliga')
            .then(response => {
                setLaligaStandings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'laligaStandings' + i} className="col">
                                <div className="left">
                                    <Tippy content={e.description}><span className={`place ${e.descrLat}`}>{e.place}</span></Tippy>
                                    <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                    <span className='name'>{e.name}</span>
                                </div>
                                <div className="nums">
                                    <span className="games">{e.games}</span>
                                    <div className="forAgainst">
                                        <span className='for'>{e.goalsFor}</span>
                                        <span>:</span>
                                        <span className='against'>{e.goalsAgainst}</span>
                                    </div>
                                    <div className="points">{e.points}</div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/bundesliga')
            .then(response => {
                setBundesligaStandings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'bundesligaStandings' + i} className="col">
                                <div className="left">
                                    <Tippy content={e.description}><span className={`place ${e.descrLat}`}>{e.place}</span></Tippy>
                                    <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                    <span className='name'>{e.name}</span>
                                </div>
                                <div className="nums">
                                    <span className="games">{e.games}</span>
                                    <div className="forAgainst">
                                        <span className='for'>{e.goalsFor}</span>
                                        <span>:</span>
                                        <span className='against'>{e.goalsAgainst}</span>
                                    </div>
                                    <div className="points">{e.points}</div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/seriea')
            .then(response => {
                setSerieaStandings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'serieaStandings' + i} className="col">
                                <div className="left">
                                    <Tippy content={e.description}><span className={`place ${e.descrLat}`}>{e.place}</span></Tippy>
                                    <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                    <span className='name'>{e.name}</span>
                                </div>
                                <div className="nums">
                                    <span className="games">{e.games}</span>
                                    <div className="forAgainst">
                                        <span className='for'>{e.goalsFor}</span>
                                        <span>:</span>
                                        <span className='against'>{e.goalsAgainst}</span>
                                    </div>
                                    <div className="points">{e.points}</div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/ligue1')
            .then(response => {
                setLigue1Standings(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'ligue1Standings' + i} className="col">
                            <div className="left">
                                <Tippy content={e.description}><span className={`place ${e.descrLat}`}>{e.place}</span></Tippy>
                                <LazyLoad offset={800}><Tippy content={e.name}><img loading="lazy" src={e.logo} alt={e.name} /></Tippy></LazyLoad>
                                <span className='name'>{e.name}</span>
                            </div>
                            <div className="nums">
                                <span className="games">{e.games}</span>
                                <div className="forAgainst">
                                    <span className='for'>{e.goalsFor}</span>
                                    <span>:</span>
                                    <span className='against'>{e.goalsAgainst}</span>
                                </div>
                                <div className="points">{e.points}</div>
                            </div>
                        </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, []);

    return (
        <div className='table5xn' id='standings'>
            <section id='standingsQckNav'>
                <h2 className="sectionName">  - </h2>
                <Swiper navigation spaceBetween={50} grabCursor={true} breakpoints={{280: {slidesPerView: 1}, 768: {slidesPerView: 2}, 1024: {slidesPerView: 3}}}>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}>
                                <Tippy content=''>
                                    <img loading="lazy" src={rplLogo} alt="" />
                                </Tippy>
                            </LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {rplStandings && rplStandings.length > 0 ? rplStandings : <div className='noData'> </div>}
                        <Link to="/league/rpl/standings"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}><Tippy content=''><img loading="lazy" src={eplLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {eplStandings && eplStandings.length > 0 ? eplStandings : <div className='noData'> </div>}
                        <Link to="/league/epl/standings"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}><Tippy content='-'><img loading="lazy" src={laligaLogo} alt="-" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {laligaStandings && laligaStandings.length > 0 ? laligaStandings : <div className='noData'> </div>}
                        <Link to="/league/laliga/standings"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}><Tippy content=' '><img loading="lazy" src={serieaLogo} alt=" " /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {serieaStandings && serieaStandings.length > 0 ? serieaStandings : <div className='noData'> </div>}
                        <Link to="/league/seriea/standings"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}><Tippy content=''><img loading="lazy" src={bundesligaLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {bundesligaStandings && bundesligaStandings.length > 0 ? bundesligaStandings : <div className='noData'> </div>}
                        <Link to="/league/bundesliga/standings"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800} height={40}><Tippy content=' 1'><img loading="lazy" src={ligue1Logo} alt=" 1" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content="  :  "><span> : </span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                        </div>
                        {ligue1Standings && ligue1Standings.length > 0 ? ligue1Standings : <div className='noData'> </div>}
                        <Link to="/league/ligue1/standings"></Link>
                    </SwiperSlide>
                </Swiper>
            </section>
        </div>
    );
};

export default StandingsSlider;