import React, { useEffect, useState } from 'react';
import './TopScores3.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, {Navigation} from "swiper";
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';

import euroQualLogo from '../../../assets/ico/euroQualLogo.webp';
import unlLogo from '../../../assets/ico/unlLogo.webp';
import person from '../../../assets/ico/person.webp';

SwiperCore.use([Navigation]);

const TopScores3 = () => {
    const [euroQualTopScores, setEuroQualTopScores] = useState(); 
    const [unlTopScores, setUnlTopScores] = useState();

    useEffect(() => { 
        const fetchData = async () => {
            await axios.get('/standings/unlTS')
            .then(response => {
                setUnlTopScores(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'unlTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player} /></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });

            await axios.get('/standings/euroqualTS')
            .then(response => {
                setEuroQualTopScores(response.data && response.data.splice(0, 8).map((e, i) => {
                    return <div key={'euroQualTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player} /></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }
        
        fetchData();
    }, []);

    return (
        <div className='table6xn' id="topScores3">
            <section id='topScores3QckNav'>
                <h2 className="sectionName"> -  </h2>
                <Swiper navigation grabCursor={true} slidesPerView={1}>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=' .'><img loading="lazy" src={euroQualLogo} alt=" ." /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {euroQualTopScores && euroQualTopScores.length > 0 ? euroQualTopScores : <div className='noData'> </div>}
                        <Link to="/league/eu-qualification/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=''><img loading="lazy" src={unlLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {unlTopScores && unlTopScores.length > 0 ? unlTopScores : <div className='noData'> </div>}
                        <Link to="/league/unl/topscores"></Link>
                    </SwiperSlide>
                </Swiper>
            </section>
        </div>
    );
};

export default TopScores3;