import React, { useEffect, useState } from 'react';
import './TopScores.css';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, {Navigation} from "swiper";
import axios from 'axios';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';

import rplLogo from '../../../assets/ico/rplLogo.webp';
import eplLogo from '../../../assets/ico/eplLogo.webp';
import laligaLogo from '../../../assets/ico/laligaLogo.webp';
import bundesligaLogo from '../../../assets/ico/bundesligaLogo.webp';
import serieaLogo from '../../../assets/ico/serieaLogo.webp';
import ligue1Logo from '../../../assets/ico/ligue1Logo.webp';
import person from '../../../assets/ico/person.webp';

SwiperCore.use([Navigation]);

const TopScoresSlider = () => {
    const [rplTopScores, setRplTopScores] = useState(); 
    const [eplTopScores, setEplTopScores] = useState(); 
    const [laligaTopScores, setLaligaTopScores] = useState(); 
    const [bundesligaTopScores, setBundesligaTopScores] = useState(); 
    const [serieaTopScores, setSerieaTopScores] = useState(); 
    const [ligue1TopScores, setLigue1TopScores] = useState(); 

    useEffect(() => { 
        const fetchData = async () => {
            await axios.get('/standings/rplTS')
            .then(response => {
                setRplTopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'rplTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={e.img} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/eplTS')
            .then(response => {
                setEplTopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'eplTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/laligaTS')
            .then(response => {
                setLaligaTopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'laligaTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/bundesligaTS')
            .then(response => {
                setBundesligaTopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'bundesligaTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/serieaTS')
            .then(response => {
                setSerieaTopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'serieaTopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
    
            await axios.get('/standings/ligue1TS')
            .then(response => {
                setLigue1TopScores(response.data && response.data.splice(1, 8).map((e, i) => {
                    return <div key={'ligue1TopScores' + i} className="col">
                                <div className="left">
                                    <span className="place">{e.place}</span>
                                    <LazyLoad offset={800}><Tippy content={e.player}><img loading="lazy" src={person} alt={e.player}/></Tippy></LazyLoad>
                                    <span className='name'>{e.player}</span>
                                </div>
                                <div className="tLogoName">
                                    <LazyLoad offset={800}><Tippy content={e.tName}><img loading="lazy" src={e.tLogo} alt={e.tName} /></Tippy></LazyLoad>
                                </div>
                                <div className="nums">
                                    <span className="goals">{e.goals ? e.goals : '0'}</span>
                                    <span>{e.pen === '(undefined' ? '(0)' : e.pen}</span>
                                    <span>{e.games}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }
        
        fetchData();
    }, []);

    return (
        <div className='table6xn'>
            <section id='topScoresQckNav'>
                <h2 className="sectionName"> - </h2>
                <Swiper navigation spaceBetween={50} grabCursor={true} breakpoints={{280: {slidesPerView: 1}, 768: {slidesPerView: 2}, 1024: {slidesPerView: 3}}}>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=''><img loading="lazy" src={rplLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {rplTopScores && rplTopScores.length > 0 ? rplTopScores : <div className='noData'> </div>}
                        <Link to="/league/rpl/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=''><img loading="lazy" src={eplLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {eplTopScores && eplTopScores.length > 0 ? eplTopScores : <div className='noData'> </div>}
                        <Link to="/league/epl/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=' '><img loading="lazy" src={laligaLogo} alt=" " /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {laligaTopScores && laligaTopScores.length > 0 ? laligaTopScores : <div className='noData'> </div>}
                        <Link to="/league/laliga/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=' '><img loading="lazy" src={serieaLogo} alt=" " /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {serieaTopScores && serieaTopScores.length > 0 ? serieaTopScores : <div className='noData'> </div>}
                        <Link to="/league/seriea/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=''><img loading="lazy" src={bundesligaLogo} alt="" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {bundesligaTopScores && bundesligaTopScores.length > 0 ? bundesligaTopScores : <div className='noData'> </div>}
                        <Link to="/league/bundesliga/topscores"></Link>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="lLogo">
                            <LazyLoad offset={800}><Tippy content=' 1'><img loading="lazy" src={ligue1Logo} alt=" 1" /></Tippy></LazyLoad>
                        </div>
                        <div className="head">
                            <Tippy content=""><span>#</span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=""><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                            <Tippy content=" "><span></span></Tippy>
                        </div>
                        {ligue1TopScores && ligue1TopScores.length > 0 ? ligue1TopScores : <div className='noData'> </div>}
                        <Link to="/league/ligue1/topscores"></Link>
                    </SwiperSlide>
                </Swiper>
            </section>
        </div>
    );
};

export default TopScoresSlider;